'use client'

import { motion } from 'framer-motion'
import { Calendar, Phone, Eye, ArrowRight, BookOpen } from 'lucide-react'

type CTAIconType = 'book' | 'call' | 'view' | 'arrow' | 'menu'

interface CTAButtonProps {
  text: string
  icon?: CTAIconType
  accentColor?: string
  onClick?: () => void
  className?: string
}

/**
 * Get icon component for CTA type
 */
function getIcon(type: CTAIconType) {
  switch (type) {
    case 'book':
      return Calendar
    case 'call':
      return Phone
    case 'view':
      return Eye
    case 'menu':
      return BookOpen
    case 'arrow':
    default:
      return ArrowRight
  }
}

/**
 * CTAButton - Primary action button at bottom of slide
 * 
 * Specs:
 * - Width: Full width (w-full)
 * - Background: Accent color (default: bg-red-600)
 * - Text: White, semibold, centered
 * - Border: rounded-full
 * - Icon: 16x16px, left of text
 * - Gap: gap-2 between icon and text
 * - Shadow: shadow-lg
 * 
 * @see SLYDESBUILD.md for full specification
 */
export function CTAButton({ 
  text, 
  icon = 'arrow', 
  accentColor = 'bg-red-600',
  onClick,
  className = '' 
}: CTAButtonProps) {
  const Icon = getIcon(icon)
  
  return (
    <motion.button
      className={`w-full ${accentColor} text-white font-semibold py-3 px-6 rounded-full flex items-center justify-center gap-2 shadow-lg ${className}`}
      whileHover={{ scale: 1.02 }}
      whileTap={{ scale: 0.98 }}
      onClick={(e) => {
        e.stopPropagation()
        onClick?.()
      }}
    >
      <Icon className="w-4 h-4" />
      {text}
    </motion.button>
  )
}

